/* CSS Всплывающего окна */

.main {
    background: #aaa url(../images/bg.jpg) no-repeat;
    width: 100px;
    height: 40px;
    margin: 50px auto;
}
.panel {
    background-color: #444;
    height: 34px;
    padding: 10px;
}
.panel {
	 a#login_pop,
 	 a#join_pop  {
	    border: 2px solid #aaa;
	    color: #fff;
	    display: block;
	    float: right;
	    margin-right: 10px;
	    padding: 5px 10px;
	    text-decoration: none;
	    text-shadow: 1px 1px #000;
	    border-radius: 10px;
	}
}
a#login_pop,a#join_pop {
	&:hover {
    	border-color: #eee;
	}
}
.overlay {
    background-color: rgba(0, 0, 0, 0.6);
    bottom: 0;
    cursor: default;
    left: 0;
    opacity: 0;
    position: fixed;
    right: 0;
    top: 0;
    visibility: hidden;
    z-index: 999;    
    transition: opacity .5s;

    &:target {
    	visibility: visible;
   		opacity: 1;

   		+.popup {
   			top: 50%;
    		opacity: 1;
    		visibility: visible;
   		}
    }
}

.popup {
    background-color: #fff;
    /*border: 3px solid #fff;*/
    display: inline-block;
    left: 50%;
    opacity: 0;
    /*padding: 15px;*/
    position: fixed;
    text-align: justify;
    top: 40%;
    visibility: hidden;
    z-index: 999;
    transform: translate(-50%, -50%);
    border-radius: 10px;
    box-shadow: 0 1px 1px 2px rgba(0, 0, 0, 0.4) inset;
    transition: opacity .5s, top .5s;


}

.close {
    background-color: rgba(0, 0, 0, 0.8);
    height: 30px;
    line-height: 30px;
    position: absolute;
    right: 0;
    text-align: center;
    text-decoration: none;
    top: -15px;
    width: 30px;
    border-radius: 15px;

    &:before {
    	color: rgba(255, 255, 255, 0.9);
	    content: "X";
	    font-size: 24px;
	    text-shadow: 0 -1px rgba(0, 0, 0, 0.9);
    }

    &:hover {
    	background-color: rgba(64, 128, 128, 0.8);
    }
}





