/* Отзывы */
.reviews-block {
	display: flex;
	margin: 50px 0;
	justify-content: space-around;
	
}

.reviews {
	margin: 70px 0;
		
}

.review {
	width: 305px;
}

.img1 {
	background: url(../forsite/client1.png) center;
}
.img2 {
	background: url(../forsite/client2.png) center;
}
.img3 {
	background: url(../forsite/client3.png) center;
}

.client-photo {
	width: 305px;
	height: 305px;
	border:1px solid #aaa;
	border-radius: 50% 50%;
	box-shadow: inset 0px 0px 70px 10px #6dcff6;

}


 .review-text {
   margin: 20px 0;
   background: #f1e7d0;
   border: 1px solid #aaa;
   border-radius: 5px;
   position: relative;
   padding: 10px;
   width: 300px;
   text-align: center;
   margin-bottom: 10px;
   line-height: 1.5em;
   box-shadow: 0px 10px 10px -4px #999;
}
 
.review-text:after,
.review-text:before {
   content: '';
   border: solid transparent;
   position: absolute;
   left: 30px;
    margin-left: -12px; 
   top: -25px;
   width: 0;
}
 .review-text:before {
   border-bottom-color: #aaa;
   border-width: 12px;

} 
 .review-text:after {
   border-bottom-color: #f1e7d0;
   border-width: 13px;
    margin-left: -13px; 
} 

